::-webkit-scrollbar {
    width: 0px; /* Remove scrollbar space */
    background: transparent; /* Optional: just make scrollbar invisible */
}

img {
    max-width: 100%;
    height: auto;
    width: auto\9; /* ie8 */
    max-height: 100%
}

.active {
    background-color: red;
    border-radius: 50%;
    border: 5px solid black;
    padding: 0px;
    height: 20px;
    width: 20px;
}

.nonactive {
    border-radius: 50%;
    background-color: lightgrey;
    border: 5px solid black;
    padding: 0px;
    height: 20px;
    width: 20px;
}


#topbar ul {
    background-color:red;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

#topbar ul li {
    float: left;
    width: 10%;
    text-align: center;
}

#topbar ul li a {
    display: block;
    width: 100%;
    background-color: red;
    text-decoration: none;
    color: white;
    border: 1px solid white;
    padding-top: 5%;
    padding-bottom: 5%;
    font-size: 1.5vw;
    overflow: auto;
    font-family: fantasy;
}

#topbar ul li a:hover{
    background-color: yellow;
    color: black;
}

#menu {
    width: 100%;
    padding-top: 50px;
    position: relative;
    overflow: auto;
    background-color: red;
}

#head {
    width: 100%;
    height: 50px;
    background-image: url('./Resources/Forza.jpg');
    background-repeat: repeat;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}

#signInBtn {
    font-size: 2vw;
    font-family: fantasy;
    border: none;
    padding: 10%;
    overflow: auto;
    color: white;
    background:  radial-gradient(#272fa5, #02c3f3);
    border-radius: 0 0 15px 15px;
    z-index: 10;
}

#signInBtn:hover {
    color:yellow;
    background: radial-gradient(#f82509, #f702d6);
}

#signIn {
    position: absolute;
    height: 50%;
    right: 1%;
    top: 0;
    overflow: auto;
}

#signInForm {
    text-align: center;
    padding: 10%;
    color:white;
    font-size: 2vw;
    position: absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50%,-50%);
}

#logIn{
    width: 50%;
    padding-top: 34%;
    background: radial-gradient(red, black);
    position: fixed;
    display: none;
    vertical-align: middle;    
    z-index: 100;
    transform:translate(-50%,-50%);
}

#logInBtn {
    border: solid 2px red;
    font-size: 1.5vw;
    color: red;
    background-color: black;
}

#logInBtn:hover {
    border: solid 2px yellow;
    color: yellow;
    background-color: black;
}

#signUpLink {
    color: white;
    text-decoration: none;
    font-size: 2vw;
    padding: 1px;
}

#signUpLink:hover {
    color: yellow;
}

#cancelBtn {
    color: white;
    background-color:red;
    width: 10%;
    padding: 1px;
    text-align: center;
    font-size: 1vw;
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
}


#title {
    width: 100%;
    text-align: center;
    padding: 0px;
    padding-top: 10%;
    padding-bottom: 10%;
    position: relative;
    background-image: url("./Resources/Night2.jpg");
}

#title_text {
    width: 50%;
    display: inline-block;
    overflow: auto;
    font-size: 5vw;
    font-family: fantasy;
}

#cover {
    background-color:black;
    width: 100%;
    height: 100%;
    top: 0;
    position: absolute;
}

.pictureSlide {
    width: 100%;
    overflow: hidden;
    position: relative;
    background-color:black;
}

#pictureSlide img {
    opacity: 0.9;
}

#LBTN, #RBTN {
    height: 100%;
    width: 2%;
    position: absolute;
    background-color: white;
    z-index: 10;
    opacity: 0.2;
    font-size: 2vw;
    text-align: center;
    padding-top: 25%;
}

#LBTN {
    left: 0px;
}

#RBTN {
    right: 0px;
}

#LBTN:hover,#RBTN:hover {
    background-color: red;
    color: white
}

#text1, #text2, #text3, #text4, #text5, #text6 {
    max-width: 100%;
    width: 96%;
    padding-top: 3%;
    padding-bottom: 3%;
    padding-right: 2%;
    padding-left: 2%;
    border: none;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    background-color: black;
}
#magicText {
    background-image: url('./Resources/Night.jpg')
}
#text1 {
    text-align: left;
    color: white;
    font-size: 3vw;
}
#text2 {
    text-align: right;
    color: yellowgreen;
    font-size: 3.5vw;
}
#text3 {
    text-align: center;
    color: palevioletred;
    font-size: 4.5vw;
}
#text4 {
    text-align: right;
    color: darkviolet;
    font-size: 4vw;
}
#text5 {
    text-align: left;
    color: cornflowerblue;
    font-size: 3.5vw;
}
#text6 {
    text-align: center;
    color: gold;
    font-size: 4.5vw;
}

#activity_title {
    text-align: center;
    width: 100%;
    padding-top: 5%;
    padding-bottom: 5%;
    color: orangered;
    background-color: black;
    font-size: 5vw;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}

.stat_num {
    color: orangered;
    font-size: 5vw;
}

#data_into {
    background-color: black;
    color: white;
    font-size: 3.5vw;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    width: 100%;
    text-align: center;
    padding-top: 3%;
    padding-bottom: 3%;
}

#feedback {
    background-color: black;
    color: white;
    text-align: left;
    width: 98%;
    min-height: 100px;
    float: left;
    font-weight: bold;
    font-size: 2vw;
    padding-left: 1%;
    border-style: solid solid none solid;
    border-width: 1px 1px 0px 5px;
    border-color: #0077FF;
}

#commentArea {
    background-color: black;
    color: white;
    width: 100%;
    overflow: auto;
    max-height: 500px;
}

#commentTitle {
    font-family: 'Roboto', sans-serif;
    text-align: center;
    font-size: 4vw;
    padding: 0px;
}
#comment {
    background-color: black;
    color: white;
    width: 95%;
    overflow: auto;
    word-wrap: break-word;
    max-width: 95%;
    border-style: solid solid none solid;
    border-width: 1px 1px 0px 5px;
    border-color: #0077FF;
    padding-left: 4%;
    font-size: 2vw;
    font-weight: bold;
}

#like {
    background-color: #f1600c;
    color: white;
    padding-top: 1%;
    padding-bottom: 1%;
    float: right;
    font-size: 1vw;
    overflow: auto;
}


#submitBtn, #clearBtn {
    width: 10%;
    padding-top: 1%;
    padding-bottom: 1%;
    padding-left:0px;
    float: left;
    color: white;
    background-color: dodgerblue;
    border-style: solid;
    border-width: 3px;
    border-color: orangered;
    font-size: 1vw;
    font-weight: bold;
    overflow: hidden;
}

.video {
    text-align: center;
    background-color: black;
    position: relative;
    color: white;
    font-size: 2vw;
    font-family: 'Roboto', sans-serif;
    overflow: auto;
    display: none;
}

#vd1, #vd2, #vd3{
    width : 100%;
    height: calc(100vw * 0.5625);
    right: 0;
    left: 0;
    bottom: 0;
}

#expandBtn{
    width: 100%;
    text-align: center;
    padding-top:1%;
    padding-bottom: 1%;
    font-size: 2vw;
    font-weight: bold;
    background-color: darkgray;
    color: black;
    border: none;
}

#input {
    width: 2%;
    height: 2%;
}

#game {
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

#param_bar {
    border: 2px white solid;
    width: 18%;
    font-size: 1.5vw;
    float: left;
    background-color: lightgrey;
    overflow: auto;
}

#speedPad {
    border: 2px white solid;
    width: 18%;
    float: right;
    font-size: 1.5vw;
    background-color: lightgrey;
    text-align: left;
    overflow: auto;
}

#gameZone {
    background-color: black;
    border: 5px rgb(189, 131, 131) dashed;
    width: 99%;
    padding-top: 1%;
    padding-bottom: 1%;
    overflow: hidden;
    position: relative;
}

#game {
    overflow: auto;
    background-color:black;
}

#gameStart {
    font-size: 1.5vw;
    border: none;
    background-color: blue;
    color:white;
}

#gameStart:hover {
    background-color: red;
    color:yellow;
}

.label {
    color: red;
    background-color: white;
}

.road {
    width: 4%;
    background-color: white;
    position: absolute;
}

#lRoad {
    height: 100%;
    top: 0;
    left: 25%;
}

#rRoad {
    height: 100%;
    top: 0;
    left: 75%;
}

#bar {
    height: 50%;
    left: 50%;
    top: -20%;
}

#car {
    width: 10%;
    height: 0;
    padding-top: 15%;
    bottom: 0%;
    left: 35%;
    background-color:red;
}

#over {
    background-color:yellow;
    width: 30%;
    left: 37%;
    top: 50%;
    padding-top: 1%;
    padding-bottom: 1%;
    z-index: 10;
    position: absolute;
    display: none;
}

#inst {
    padding-left: 5%;
    padding-right: 5%;
    background-color: black;
    font-weight:bold;
    color: white;
    font-size: 1.5vw;
}

#stat {
    background-color: black;
    width: 96%;
    padding: 2%;
    text-align: left;
    font-size: 2vw;
    font-weight: bold;
    position: relative;
}

.current {
    width: 65%;
    left: 30%;
    position: absolute;
}
 

meter::-webkit-meter-bar {
    background: black;
}
meter::-webkit-meter-optimum-value {
    background: linear-gradient(to right, #7a0a06, #f31202);
}
meter::-webkit-meter-suboptimum-value {
    background: linear-gradient(to right, #7a0a06, #f31202);
}
meter::-webkit-meter-even-less-good-value {
    background: linear-gradient(to right, #7a0a06, #f31202);
}

input[type=range]{
    -webkit-appearance: none;
    width: 80%;
}

input[type=range]::-webkit-slider-runnable-track {
    height: 5px;
    background: rgb(252, 253, 251);
    border: none;
    border-radius: 3px;
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 16px;
    width: 16px;
    border-radius: 30%;
    background:black;
    margin-top: -4px;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: rgb(253, 77, 7);
}
